import React from 'react';
import ReactDOM from 'react-dom';
import { compose, toClass } from 'recompose';
import * as Components from './components';
import configureApollo from './lib/configureApollo';
import connectI18n from './lib/connectI18n';
import withData from './lib/apollo/withData';

export default class UnchainedWidgetFactory {
  constructor(endpoint = 'https://unchained.shop/graphql', locale = 'en_US') {
    this.endpoint = endpoint;
    this.locale = locale;
    configureApollo(this.endpoint);
  }
  render(element, Component, aProps, callback) {
    const withContainer = compose(
      withData,
      connectI18n(this.locale),
    );

    const Test = toClass(withContainer(Component));
    ReactDOM.render(<Test {...aProps} />, element, callback);
  }
}

export { Components };



